.card__button {
  display: block;
  color: #011C40;
  background-color: #F9FBFD;
  padding: 10px;
  text-align: center;
  font-weight: 600; }

.booking {
  position: relative;
  text-align: start; }
  @media (max-width: 1023px) {
    .booking {
      display: block;
      text-align: center; } }

.booking__container {
  display: grid;
  grid-template-columns: 1fr auto;
  padding: 50px 80px;
  align-items: center; }
  @media (max-width: 1023px) {
    .booking__container {
      display: block; } }

.booking img {
  position: absolute;
  width: 100%;
  height: 555px;
  z-index: -1;
  object-fit: cover; }
  @media (max-width: 1023px) {
    .booking img {
      height: 805px; } }

.booking__form {
  width: 403px; }
  @media (max-width: 1023px) {
    .booking__form {
      margin: 0 auto;
      margin-top: 50px; } }
  @media (max-width: 767px) {
    .booking__form {
      width: 100%;
      margin-top: 35px; } }

.booking h2 {
  font-size: 40px;
  line-height: 60px;
  font-weight: 700;
  text-align: inherit;
  margin-bottom: 5px; }
  @media (max-width: 1023px) {
    .booking h2 {
      font-size: 34px;
      line-height: 51px; } }
  @media (max-width: 767px) {
    .booking h2 {
      font-size: 24px;
      line-height: 31px;
      margin-bottom: 15px; } }

.booking p {
  font-size: 18px;
  line-height: 27px;
  margin: 0;
  max-width: 360px; }
  @media (max-width: 1023px) {
    .booking p {
      margin: 0 auto; } }
  @media (max-width: 767px) {
    .booking p {
      font-size: 16px;
      line-height: 22px;
      max-width: 260px; } }

.form {
  background-color: #011C40;
  box-shadow: 0px 4px 45px rgba(1, 28, 64, 0.15);
  padding: 50px; }
  @media (max-width: 767px) {
    .form {
      padding: 40px 30px; } }

.form legend {
  font-size: 18px;
  line-height: 27px;
  font-weight: 500;
  color: #F9FBFD;
  margin-top: -5px;
  margin-bottom: 27px; }
  @media (max-width: 1023px) {
    .form legend {
      text-align: start; } }
  @media (max-width: 767px) {
    .form legend {
      text-align: center;
      line-height: 25px; } }

.form input {
  display: block;
  width: 100%;
  background-color: #F9FBFD;
  border: none;
  margin-bottom: 20px;
  padding: 11px 16px;
  font-size: 14px;
  line-height: 21px; }
  .form input:hover {
    background-color: #E3ECF6; }
  .form input:focus {
    background-color: #E3ECF6;
    outline: 0; }
  .form input::placeholder {
    color: rgba(1, 28, 64, 0.7); }

.form input[type="checkbox"] {
  display: none; }

.form input[type="checkbox"] + label {
  display: flex;
  align-items: center;
  margin-bottom: 30px;
  font-size: 10px;
  line-height: 12px;
  color: #F9FBFD; }
  @media (max-width: 767px) {
    .form input[type="checkbox"] + label {
      text-align: start;
      line-height: 10px; } }

.form input[type="checkbox"] + label::before {
  display: inline-block;
  content: "";
  flex-shrink: 0;
  background-color: #F9FBFD;
  width: 18px;
  height: 18px;
  margin-right: 5px; }
  @media (max-width: 767px) {
    .form input[type="checkbox"] + label::before {
      width: 20px;
      height: 20px;
      margin-right: 8px; } }

.form input[type="checkbox"] + label:hover::before {
  background-color: #E3ECF6; }

.form input[type="checkbox"]:checked + label::before {
  background-image: url("../img/svg/form-check-mark.svg");
  background-repeat: no-repeat;
  background-position: 50%; }

.form button {
  border: none;
  background-color: #F9FBFD;
  color: #011C40;
  width: 100%;
  font-size: 14px;
  line-height: 21px;
  font-weight: 600;
  padding: 11px; }
  .form button:hover {
    background-color: #E3ECF6; }

.page-footer {
  background-color: #011C40; }

.page-footer__container {
  display: flex;
  flex-wrap: nowrap;
  height: 138px;
  align-items: center;
  justify-content: space-between; }
  @media (max-width: 767px) {
    .page-footer__container {
      display: block;
      height: auto;
      padding: 40px 15px; } }

.page-footer__social {
  width: min-content; }

.social__list {
  list-style: none;
  display: flex;
  padding: 0; }

.social__item {
  margin-left: 28px; }
  @media (max-width: 1023px) {
    .social__item {
      margin-left: 23px; } }
  .social__item:first-child {
    margin: 0; }

.social__link {
  color: #F9FBFD; }
  .social__link:hover {
    color: #E3ECF6; }

